import React, {Component} from 'react';

class Albums extends Component {
    constructor(props){
        super(props);
    }

    render(){
        return (
            <div>
                <div className="albumContainer">
                {this.props.albums.map(a => {
                    return <div className="album" key={a.slug}>
                        <h2 className="text albumTitle">{`${a.title} - ${a.releaseType}`}</h2>
                        <div className="oneThird">
                            <img src={this.props.urlFor(a.cover.asset._ref)}/>
                            {
                            a.bandcamp?
                                <form action={a.bandcamp}>
                                    <input className="callToAction text" type="submit" value="Buy on Bandcamp"/>
                                </form>: <div>Pico</div>
                            }
                            {
                                a.spotify?
                                <form action={a.spotify}>
                                    <input className="callToAction text" type="submit" value="Listen on Spotify"/>
                                </form>: <div></div>
                            }
                        </div>
                        <div className="twoThirds">
                            <p className="description text">{a.description}</p>
                        </div>
                    </div>
                })}
                </div>
                <style jsx>{`
                    img {
                        width: 70%;
                        padding: 0;
                    }
                    .albumContainer {
                        display: flex;
                        flex-direction: column;
                        width: 100%;
                        padding: 20px 20px 0 20px;
                        border: 1px solid white;
                    }
                    .album {
                        width: 100%;
                        padding: 10px 2% 10px 2%;
                    }
                    .twoThirds {
                        width: 70%;
                        float: left;
                    }
                    .oneThird {
                        width: 30%;
                        float: left;
                    }
                    p {
                        width: 90%;
                        margin-bottom: 10px;
                    }
                    .text{
                        font-family: 'Encode Sans Expanded', sans-serif;
                        color: white;
                    }
                    .albumTitle {
                        text-align: center;
                    }
                    .callToAction {
                        width: 70%;
                        height: 30px;
                        background-color: #519ead;
                        margin: 10px 0 10px 0;
                    }
                `}</style>
            </div>
        );
    }
}
export default Albums;
